import React, { useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import ReactVideo from "../components/ReactVideo";
import Button from "react-bootstrap/Button";
import Collapse from "react-bootstrap/Collapse";
import { projects } from "../models/project";
//

type Props = {};

const DetailProjectPage = (props: Props) => {
  const { slug } = useParams();
  console.log(slug);
  const selectedProject = projects.find((pro) => pro.id === slug);

  const navigate = useNavigate();
  const [open, setOpen] = useState(false);
  return (
    <div className="detailPage">
      <div className="aboutme__back" onClick={() => navigate("/")}>
        Home
      </div>
      <div className="detailPage__video">
        <h2>Video Demo</h2>
        <ReactVideo url={selectedProject?.youtube || ""} />
      </div>
      <div className="detailPage__detail">
        <>
          <div
            className="detailPage__button border-gradient-purple border-gradient"
            onClick={() => setOpen(!open)}
          >
            <span>Show Detail</span>
          </div>
          <Collapse in={open}>
            <div id="example-collapse-text" style={{ marginBottom: 20 }}>
              <a href={selectedProject?.deploy}>
                Deploy : {selectedProject?.deploy}
              </a>
              {selectedProject?.details.map((detail, index) => (
                <span key={index}>{detail}</span>
              ))}
            </div>
          </Collapse>
        </>
      </div>
    </div>
  );
};

export default DetailProjectPage;
