import React from "react";
import { Col, Container, Row } from "react-bootstrap";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import gmail from "../assets/img/Gmail_Logo_512px.png";
import tiktok from "../assets/img/tiktok.png";
import meter4 from "../assets/img/meter1.svg";
import facebook from "../assets/img/facebook.png";
import intagram from "../assets/img/intargram.webp";
import colorSharp from "../assets/img/color-sharp.png";
import { useNavigate } from "react-router-dom";
type Props = {};

const Contact = (props: Props) => {
  const navigate = useNavigate();
  const responsive = {
    superLargeDesktop: {
      // the naming can be any, depends on you.
      breakpoint: { max: 4000, min: 3000 },
      items: 5,
    },
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 3,
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 2,
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1,
    },
  };
  return (
    <section className="skill " id="skills">
      <Container>
        <Row>
          <Col>
            <div className="skill-bx">
              <h2>Contact Me</h2>
              <p>You can contact me by social media</p>
              <Carousel
                infinite={true}
                responsive={responsive}
                className="skill-slider"
              >
                <div
                  className="item"
                  onClick={() =>
                    window.location.assign(
                      "https://www.facebook.com/khandev.1601/"
                    )
                  }
                >
                  <img src={facebook} alt="Image" />
                  <h5>Facebook</h5>
                </div>
                <div
                  className="item"
                  onClick={() =>
                    window.location.assign(
                      "https://www.instagram.com/td_typecode/"
                    )
                  }
                >
                  <img src={intagram} alt="Image" />
                  <h5>Instagram</h5>
                </div>
                <div
                  className="item"
                  onClick={() =>
                    window.location.assign(
                      "https://www.instagram.com/td_typecode//"
                    )
                  }
                >
                  <img src={gmail} alt="Image" />
                  <h5>Gmail</h5>
                </div>
                <div className="item">
                  <img
                    src={tiktok}
                    alt="Image"
                    style={{ borderRadius: "10px" }}
                  />
                  <h5>Tiktok</h5>
                </div>
              </Carousel>
            </div>
          </Col>
        </Row>
      </Container>
      <img src={colorSharp} alt="" className="background-image-left " />
    </section>
  );
};

export default Contact;
