import React from "react";
import { Col } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
type Props = {
  title: string;
  description: string;
  imgUrl: string;
  navi: string;
};

const ProjectCard = ({ title, description, imgUrl, navi }: Props) => {
  const navigate = useNavigate();
  return (
    <Col
      size={12}
      sm={6}
      md={4}
      onClick={() => navigate(`detail-project/${navi}`)}
    >
      <div className="proj-imgbx">
        <img src={imgUrl} style={{ height: "308px" }} alt="" />
        <div className="proj-txtx">
          <h4>{title}</h4>
          <span>{description}</span>
        </div>
      </div>
    </Col>
  );
};

export default ProjectCard;
