import React, { useEffect, useState } from "react";
import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import { useNavigate } from "react-router-dom";

import navIcon1 from "../assets/img/nav-icon1.svg";
import navIcon2 from "../assets/img/nav-icon2.svg";
import mylogo from "../assets/img/my-logo.png";
import navIcon3 from "../assets/img/nav-icon3.svg";

type Props = {};

const NavbarContent = (props: Props) => {
  const navigate = useNavigate();
  const [activeLink, setActiveLink] = useState<string>("");
  const [scrolled, setScrolled] = useState<boolean>(false);
  const handleDownload = () => {
    // var link = document.createElement("a");
    // const doc = new jsPDF();
    // // doc.text("Hello world!", 10, 10);
    // doc.existsFileInVFS("../assets/CV-Nguyen Duc Thien.pdf", 10, 10);
    // doc.save("a4.pdf");
  };
  useEffect(() => {
    const onScroll = (): void => {
      if (window.screenY > 50) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    };
    window.addEventListener("scroll", onScroll);
    return () => {
      window.removeEventListener("scroll", onScroll);
    };
  }, []);
  const onUpdateActiveLink = (content: string) => {
    setActiveLink(content);
  };
  return (
    <Navbar expand="lg" className={scrolled ? "scrolled" : ""}>
      <Container>
        <Navbar.Brand href="#">
          <img
            src={mylogo}
            alt="Logo"
            style={{ height: "84px", width: "137px" }}
          />
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav">
          <span className="navbar-toggler-icon"></span>
        </Navbar.Toggle>
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link
              onClick={() => onUpdateActiveLink("home")}
              href="#home"
              className={
                activeLink === "home" ? "active navbar-link" : "navbar-link"
              }
            >
              Home
            </Nav.Link>
            <Nav.Link
              onClick={() => {
                onUpdateActiveLink("aboutme");
                navigate("/about-me");
              }}
              className={
                activeLink === "aboutme" ? "active navbar-link" : "navbar-link"
              }
            >
              About me
            </Nav.Link>
            <Nav.Link
              onClick={() => onUpdateActiveLink("skills")}
              href="#skills"
              className={
                activeLink === "skills" ? "active navbar-link" : "navbar-link"
              }
            >
              Skills
            </Nav.Link>
            <Nav.Link
              onClick={() => onUpdateActiveLink("projects")}
              href="#projects"
              className={
                activeLink === "projects" ? "active navbar-link" : "navbar-link"
              }
            >
              Projects
            </Nav.Link>
          </Nav>
          <span className="navbar-text">
            <div className="social-icon">
              <a href="#">
                <img src={navIcon1} alt="" />
              </a>
              <a href="#">
                <img src={navIcon2} alt="" />
              </a>
              <a href="#">
                <img src={navIcon3} alt="" />
              </a>
            </div>
            <button className="vvd" onClick={() => handleDownload()}>
              <a href="CV.pdf" download="CV.pdf">
                DOWNLOAD CV
              </a>
            </button>
          </span>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};

export default NavbarContent;
