import React from "react";
import airbnb from "../assets/img/airbnb1.webp";
import netfake from "../assets/img/net-fake.png";
import pets from "../assets/img/pets.png";
import master from "../assets/img/master-work.png";
import cloth from "../assets/img/cloth.png";
import todo from "../assets/img/todo.png";
import ProjectCard from "./ProjectCard";
import colorSharp2 from "../assets/img/color-sharp2.png";

import { Container, Nav, Tab } from "react-bootstrap";
import { Row } from "react-bootstrap";
import { Col } from "react-bootstrap";
type Props = {};

const Project = (props: Props) => {
  const projects = [
    {
      title: "AirBnB",
      description: "A web clone for tourists",
      imgUrl: airbnb,
      navi: "airbnb-clone",
    },
    {
      title: "Net Fake",
      description: "Website to book ticket",
      imgUrl: netfake,
      navi: "net-fake",
    },
    {
      title: "Pets Shop",
      description: "Buy and see many lovely pets",
      imgUrl: pets,
      navi: "pets-shop",
    },
    {
      title: "Mater Work",
      description: "Mater work is built by pure HTML and CSS",
      imgUrl: master,
      navi: "master-work",
    },
    {
      title: "Cloth Store",
      description: "Shopping in happiness",
      imgUrl: cloth,
      navi: "cloth-store",
    },
    {
      title: "Todo App",
      description: "Schedule your work reasonablely",
      imgUrl: todo,
      navi: "todo-app",
    },
  ];
  return (
    <section className="project" id="projects">
      <Container>
        <Row>
          <Col>
            <h2>Projects</h2>
            <p>
              Lorem ipsum dolor sit amet consectetur adipisicing elit. Officia
              minus vel tenetur vitae consequatur itaque saepe?
            </p>
            <Tab.Container id="projects-tabs" defaultActiveKey={"first"}>
              <Nav variant="pills" className="nav-pills mb-5 justify-content">
                <Nav.Item>
                  <Nav.Link eventKey="first">Tab One</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                  <Nav.Link eventKey="second">Tab Two</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                  <Nav.Link eventKey="third">Tab Three</Nav.Link>
                </Nav.Item>
              </Nav>
              <Tab.Content>
                <Tab.Pane eventKey={"first"}>
                  <Row>
                    {projects.map((project, index) => {
                      return (
                        <ProjectCard
                          navi={project.navi}
                          key={index}
                          title={project.title}
                          description={project.description}
                          imgUrl={project.imgUrl}
                        />
                      );
                    })}
                  </Row>
                </Tab.Pane>
                <Tab.Pane eventKey={"second"}>Lorem</Tab.Pane>
                <Tab.Pane eventKey={"third"}>Lorem</Tab.Pane>
              </Tab.Content>
            </Tab.Container>
          </Col>
        </Row>
      </Container>
      <img src={colorSharp2} className="background-image-right" alt="" />
    </section>
  );
};

export default Project;
