import React, { Fragment, useEffect, useState } from "react";
import avatar from "../assets/img/avatar.jpg";
import { content } from "../models/aboutMe";
import { CircleFill } from "react-bootstrap-icons";
import Loading from "../components/Loading";
import { useNavigate } from "react-router-dom";
type Props = {};

const AboutMePage = (props: Props) => {
  const navigate = useNavigate();
  const [loading, setLoading] = useState<boolean>(true);
  useEffect(() => {
    setTimeout(() => {
      setLoading(false);
    }, 1000);
  }, []);
  return (
    <>
      <div className="aboutme">
        <div className="aboutme__back" onClick={() => navigate("/")}>
          Home
        </div>
        {loading ? (
          <Loading />
        ) : (
          <>
            <div className="titleContainer">
              <div className="imageContainer">
                <img src={avatar} alt="" />
              </div>
              <div className="contentContainer">
                <h1>Frontend Developer</h1>
                <h2>Nguyen Duc Thien</h2>
              </div>
            </div>
            <div className="aboutmeContent">
              <div className="aboutmeContent__left">
                {content.slice(0, 4).map((con, index) => (
                  <div key={con.id} className="aboutmeItem">
                    <div className="aboutmeItem__title">
                      <span>{con.icon}</span>
                      <h3>{con.title}</h3>
                    </div>
                    <ul className="aboutmeItem__content">
                      {con.content.map((c, index) => (
                        <li key={index}>{c}</li>
                      ))}
                    </ul>
                  </div>
                ))}
              </div>
              <div className="aboutmeContent__right">
                {content.slice(4, 7).map((con, index) => (
                  <div key={con.id} className="aboutmeItem">
                    <div className="aboutmeItem__title">
                      <h3>{con.title}</h3>
                      <span>{con.icon}</span>
                    </div>
                    <ul className="aboutmeItem__content">
                      {con.content.map((c, index) => (
                        <li key={index}>{c}</li>
                      ))}
                    </ul>
                  </div>
                ))}
              </div>
            </div>
          </>
        )}
      </div>
    </>
  );
};

export default AboutMePage;
