import React from "react";

import ReactPlayer, { ReactPlayerProps } from "react-player";
type Props = {
  url: string;
};

const ReactVideo = ({ url }: Props) => {
  return (
    <div>
      <ReactPlayer
        playing={true}
        height={400}
        width={700}
        controls={true}
        url={url}
      />
    </div>
  );
};

export default ReactVideo;
