import React from "react";
import loadingImg from "../assets/img/header-img.svg";
type Props = {};

const Loading = (props: Props) => {
  return (
    <div className="loading">
      <img
        src={loadingImg}
        alt=""
        style={{ height: "300px", width: "300px" }}
      />
    </div>
  );
};

export default Loading;
